.navMenuWrapper {
  composes: flex from '../index.css';
  width: 15%;
  height: 100vh;
  background-color: antiquewhite;
  padding: 5px;
}

.navMenuTitle {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.navMenuTitle h3 {
  margin: 15px;
}

.navMenu {
  display: flex;
  flex-direction: inherit;
  flex-wrap: wrap;
  align-items: inherit;
}

@media (max-width: 500px) {
  .navMenuWrapper {
    height: auto;
    width: 100%;
  }

  .navMenuTitle {
    flex-direction: row;
  }

  .navMenu {
    flex-direction: row;
  }
}